import React from 'react'
import Container from 'react-bootstrap/Container'
import {ReactComponent as Logo} from '../assets/imgs/logo.svg'
import Banner from '../components/Banner';

const Home = () => {
    return (
        <main>
            <Banner main={true} title={' FR  '} imgUrl={'imgs/products/ FR  .jpg'}/>

            <Banner main={false} title={'HA '} imgUrl={'imgs/products/HA -      .jpg'}/>
            <Banner main={false} title={'MATE '} imgUrl={'imgs/products/SS -     +  .jpg'}/>
            <Banner main={false} title={' '} imgUrl={'imgs/products/MM -   - + 2  .jpg'}/>
            <Banner main={false} title={'EC '} imgUrl={'imgs/products/EC -     + .jpg'}/>
        </main>
    );
};

export default Home;